import api from "./api";

// Get all businesses with filters
const getBusinesses = async (params = {}) => {
  try {
    const response = await api.get("/businesses/", { params });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch businesses" };
  }
};

// Get single business by ID
const getBusinessById = async (id) => {
  try {
    const response = await api.get(`/businesses/${id}/`);
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch business details" };
  }
};

// Create new business
const createBusiness = async (businessData) => {
  try {
    const response = await api.post("/businesses/", businessData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to create business" };
  }
};

// Update business
const updateBusiness = async (id, businessData) => {
  try {
    const response = await api.put(`/businesses/${id}/`, businessData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to update business" };
  }
};

// Delete business (soft delete)
const deleteBusiness = async (id) => {
  try {
    const response = await api.delete(`/businesses/${id}/`);
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to delete business" };
  }
};

// Get nearby businesses
const getNearbyBusinesses = async (latitude, longitude, radius = 5) => {
  try {
    const response = await api.get("/businesses/nearby/", {
      params: { latitude, longitude, radius },
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch nearby businesses" };
  }
};

// Get business reviews
const getBusinessReviews = async (businessId) => {
  try {
    const response = await api.get(`/businesses/${businessId}/reviews/`);
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch reviews" };
  }
};

// Upload business image
const uploadBusinessImage = async (businessId, imageData) => {
  try {
    const formData = new FormData();
    formData.append("image", imageData.image);
    if (imageData.caption) formData.append("caption", imageData.caption);
    if (imageData.is_primary) formData.append("is_primary", imageData.is_primary);

    const response = await api.post(
      `/businesses/${businessId}/upload_image/`,
      formData,
      {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      }
    );
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to upload image" };
  }
};

// Contact business
const contactBusiness = async (businessId, message) => {
  try {
    const response = await api.post(`/businesses/${businessId}/contact/`, {
      message,
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to send message" };
  }
};

// Get all categories
const getCategories = async () => {
  try {
    const response = await api.get("/categories/");
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch categories" };
  }
};

// Get public categories (cached)
const getPublicCategories = async () => {
  try {
    const response = await api.get("/public-categories/");
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch categories" };
  }
};

// Get top rated businesses
const getTopRatedBusinesses = async (period = "all", limit = 10) => {
  try {
    const response = await api.get("/top-rated/", {
      params: { period, limit },
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch top rated businesses" };
  }
};

// Get business statistics
const getStatistics = async () => {
  try {
    const response = await api.get("/statistics/");
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch statistics" };
  }
};

// Search businesses
const searchBusinesses = async (query, filters = {}) => {
  try {
    const response = await api.get("/search/", {
      params: { q: query, ...filters },
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to search businesses" };
  }
};

export default {
  getBusinesses,
  getBusinessById,
  createBusiness,
  updateBusiness,
  deleteBusiness,
  getNearbyBusinesses,
  getBusinessReviews,
  uploadBusinessImage,
  contactBusiness,
  getCategories,
  getPublicCategories,
  getTopRatedBusinesses,
  getStatistics,
  searchBusinesses,
};